.choices {
	min-width: 170px;
	header & {
		min-width: 80px;

		@include media('>=widescreen-mega') {
			min-width: 120px;
		}

		@include media('>=full-hd') {
			min-width: 170px;
		}
	}
	margin: 0px;
	&__inner {
		background-color: #0076ad;
		border-color: #0076ad;
		border-radius: 100px;
		min-height: 30px;
		padding: 8px 12px;
		.is-focused &,
		.is-open & {
			border-color: #0076ad;
		}
	}
	&__item--selectable {
		color: rgb(255, 255, 255,0.71);
		font-size: 13px;
		font-weight: 300;
		@include media('>=widescreen-lg') {
			font-size: 14px;
		}
		@include media('>=full-hd') {
			font-size: 18px;
		}
	}
	&__list--dropdown,
	&__list[aria-expanded] {
		background-color: #0076ad;
		border-color: #0076ad;
		.is-open & {
			border-color: #0076ad;
		}
	}

	&[data-type*=select-one] {
		&::after {
			border-color: #fff transparent transparent;
		}
		&.is-open::after {
			border-color: transparent transparent #fff;
		}
	}
}

.choices__list--dropdown .choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
	background-color: #0076ad;
	
}
.choices__list--single {
	padding: 0px;
}