.header {
  background: linear-gradient(to right, #091c2c, #737f89);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 9999;
  padding: 30px 0px;
  .cases & {
    background: rgb(9, 28, 44,0.7);
  }
  &-body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    padding: 0px 16px;
    @include media('>=desktop') {
      gap: 20px;
      padding: 0px 30px;
      flex-wrap: wrap;
    }
    @include media('>=widescreen-md') {
      gap: 10px;
      flex-wrap: nowrap;
    }
    @include media('>=widescreen-lg') {
			gap: 20px;
		}
    @include media('>=full-hd') {
      padding: 0px 80px;
    }

  }
  &-menu {
    @include media('>=desktop') {
      width: 100%;
      order: 3;
      padding-top: 20px;
      border-top: 1px solid #00a5d8;
    }
    @include media('>=widescreen-md') {
      padding-top: 0px;
      width: fit-content;
      order: unset;
      border: 0px;
    }
    &-burger {
      position: relative;
      width: 20px;
      height: 20px;
      cursor: pointer;
      border: none;
      background: none;
      display: flex;
      align-items: center;
      justify-content: center;
      @include media('>=desktop') {
				display: none;
			}
    }

    &-burger span,
    &-burger span::before,
    &-burger span::after {
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #fff;
      border-radius: 2px;
      transition: all 0.3s ease;
      content: '';
      left: 0;
    }

    &-burger span::before {
      top: -8px;
    }

    &-burger span::after {
      top: 8px;
    }

    /* Анимация при активном состоянии */
    &-burger.active span {
      background-color: transparent;
    }

    &-burger.active span::before {
      transform: rotate(45deg);
      top: 0;
    }

    &-burger.active span::after {
      transform: rotate(-45deg);
      top: 0;
    }
    &-list {
      display: flex;
      align-items: center;
      gap: 10px;
      @include media('>=desktop') {
        justify-content: space-between;
      }
      @include media('>=widescreen-md') {
        justify-content: start;
      }
      @include media('>=full-hd') {
        gap: 20px;
      }
      
      @include media('<desktop') {
        position: fixed;
        top: 88px;
        left: 0;
        width: 100vw;
        height: 100vh;
        background: linear-gradient(to right, #091c2c, #737f89);
        flex-direction: column;
        align-items: flex-start;
        gap: 20px;
        padding: 16px 40px;
        opacity: 0;
        visibility: hidden;
        transform: translateY(10px);
        transition: all 0.3s ease;
        overflow-y: auto;
        padding: 16px;
        .cases & {
          background: rgb(9, 28, 44,0.7);
        }

        &.active {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
        }
			}
      @include media('<tablet') {
        top: 70px;
      }
    }

    &-divider {
      width: 1px;
      display: none;
      height: 16px;
      background-color: #00a5d8;
      @include media('>=desktop') {
        display: block;
      }
    }

    &-link {
      color: rgb(255, 255, 255,0.71);
      font-size: 13px;
      transition: all 0.25s ease 0s;
      position: relative;

      @include media('>=widescreen-lg') {
        font-size: 14px;
      }

      &::after {
        content: '';
        position: absolute;
        bottom: -5px;
        width: 0px;
        left: 0;
        height: 1px;
        background-color: #639590;
        transition: all 0.25s ease 0s;
      }
      &:hover {
        color: #639590;
        font-weight: 700;
        &::after {
          width: 100%;
        }
      }
    }
  }

  &-search {
    display: none;
    align-items: center;
    gap: 10px;

    @include media('>=desktop') {
      display: flex;
    }
    @include media('>=widescreen-lg') {
			gap: 16px;
		}

    p {
      margin: 0px;
      font-weight: 300;
      color: rgb(255, 255, 255,0.71);
      font-size: 13px;
      @include media('>=widescreen-lg') {
        font-size: 14px;
      }
      @include media('>=full-hd') {
        font-size: 16px;
      }
    }
  }

  &-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    @include media('>=widescreen-lg') {
			gap: 16px;
		}
  }

  &-action-link {
    display: flex;
    align-items: center;
    gap: 8px;
    max-width: fit-content;
    color: rgb(255, 255, 255,0.71);
    font-weight: 300;
    line-height: 100%;
    font-size: 13px;
    transition: all 0.25s ease 0s;
    position: relative;

    @include media('>=widescreen-lg') {
      font-size: 14px;
    }
    @include media('>=full-hd') {
      font-size: 16px;
    }

    &:hover {
      color: #639590;
      font-weight: 700;
    }

    span {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: rgb(255, 255, 255,0.71);

      svg {
        display: block;
        width: 15px;
      }
    }
  }
}

