.team-section {
	background-color: #0e283b;
	padding: 40px 0px;
	@include media('>=desktop') {
		padding: 60px 0px;
	}
	@include media('>=widescreen-lg') {
		padding: 80px 0px;
	}
	@include media('>=full-hd') {
		padding: 100px 0px;
	}

	h2 {
		margin: 0px;
		font-size: 24px;
		color: #639590;
		font-weight: 400;
		@include media('>=tablet') {
			font-size: 28px;
		}
		@include media('>=desktop') {
			font-size: 32px;
		}
		@include media('>=widescreen-lg') {
			font-size: 40px;
		}
		@include media('>=full-hd') {
			font-size: 48px;
		}
	}

	&-body {
		margin-top: 20px;
		display: grid;

		@include media('>=tablet') {
			grid-template-columns: 1fr 1.5fr;
		}

		@include media('>=desktop') {
			margin-top: 40px;
		}
		@include media('>=widescreen-lg') {
			margin-top: 60px;
			grid-template-columns: 1fr 1fr;
		}
	}

	&-content {
		background-image: url('../images/team-bg.png');
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 20px;
		gap: 20px;
		@include media('>=desktop') {
			padding: 40px;
		}

		h3 {
			margin: 0px;
			font-weight: 400;
			font-size: 24px;

			@include media('>=desktop') {
				font-size: 28px;
			}
			@include media('>=widescreen-lg') {
				font-size: 32px;
			}
			@include media('>=full-hd') {
				font-size: 40px;
			}	
		}

		a {
			span {
				background-color: rgb(255, 255, 255,0.71);
				display: inline-flex;
				width: 20px;
				align-items: center;
				justify-content: center;
				text-align: center;
				height: 20px;
				border-radius: 50%;
				color: #005149;
				font-weight: 700;
				font-size: 13px;
			}
		}
	}

	&-job-title {
		margin: 0px;
		color: white;
		font-weight: 700;
		font-size: 18px;

		@include media('>=desktop') {
			font-size: 20px;
		}
		@include media('>=widescreen-lg') {
			font-size: 24px;
		}
		@include media('>=full-hd') {
			font-size: 28px;
		}
	}

	&-text {
		* {
			color: rgb(255, 255, 255,0.71);
			font-weight: 300;
			line-height: 170%;
			font-size: 14px;

			@include media('>=desktop') {
				font-size: 16px;
			}

			@include media('>=widescreen-lg') {
				font-size: 18px;
			}

			strong {
				font-weight: 700;
				color: white;
			}
		}

		& > *:not(:first-child) {
			margin-top: 6px;
		}
	}
}