.text-section {
	padding: 40px 0px;
	&.secondary {
		background-color: #0e283b;
	}
	@include media('>=desktop') {
		padding: 60px 0px;
	}
	@include media('>=widescreen-lg') {
		padding: 80px 0px;
	}
	@include media('>=full-hd') {
		padding: 100px 0px;
	}

	&-content {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		gap: 16px;

		h2 {
			color: white;
			font-weight: 400;
			font-size: 24px;
			@include media('>=tablet') {
				font-size: 28px;
				max-width: 600px;
			}
			@include media('>=desktop') {
				max-width: 700px;
				font-size: 32px;
			}
			@include media('>=widescreen-lg') {
				font-size: 40px;
				max-width: 900px;
				
			}
			@include media('>=full-hd') {
				font-size: 48px;
				max-width: 1000px;
			}
		}

		p {
			margin: 0px;
			color: rgb(255, 255, 255,0.71);
			line-height: 180%;
			font-weight: 300;
			span {
				color: #598e89;
			}
			strong {
				font-weight: 700;
			}
		}

		a {
			margin-top: 20px;
		}
	}
}