.hero-section {
	&-logo {
		padding: 0px 16px;
		max-width: 300px;
		@include media('>=desktop') {
			max-width: 440px;
			padding: 0px 60px;
		}
		@include media('>=widescreen-lg') {
			padding: 0px 80px;
			max-width: 560px;
		}
	}
	&-slide {
		position: relative;
		padding: 150px 0px 120px 0px;
		box-sizing: border-box !important;

		@include media('>=desktop') {
			padding-bottom: 150px;
		}

		&-bg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
		}
		&-content {
			position: relative;
			z-index: 2;

			h1 {
				margin: 0px;
				margin-top: 30px;
				max-width: 800px;
				color: rgb(255, 255, 255,0.71);
				font-size: 32px;
				font-weight: 400;
				max-width: fit-content;
				padding-bottom: 16px;
				border-bottom: 1px solid #00a6d8;
				span {
					color: #00a6d8;
				}
				@include media('>=tablet') {
					font-size: 40px;
				}
				@include media('>=desktop') {
					font-size: 56px;
					padding-bottom: 30px;
				}
				@include media('>=widescreen-lg') {
					margin-top: 80px;
					font-size: 72px;
				}
				@include media('>=full-hd') {
					font-size: 90px;
					padding-bottom: 40px;
				}

				&.small {
					font-size: 32px;
					@include media('>=tablet') {
						font-size: 40px;
					}
					@include media('>=desktop') {
						font-size: 50px;
					}
				}
			}
		}
		&-text {
			margin: 0px;
			margin-top: 16px;
			color: rgb(255, 255, 255,0.71);
			font-size: 16px;
			font-weight: 300;
			max-width: 285px;
			line-height: 130%;
			@include media('>=tablet') {
				font-size: 20px;
				max-width: 356px;
			}
			@include media('>=desktop') {
				margin-top: 30px;
				font-size: 24px;
				max-width: 496px;
			}
			@include media('>=widescreen-lg') {
				font-size: 32px;
				max-width: 637px;
			}
			@include media('>=full-hd') {
				font-size: 48px;
				max-width: 796px;
				margin-top: 40px;
			}
		}

		&-buttons {
			margin-top: 30px;
			display: flex;
			align-items: center;
			gap: 30px;
			flex-wrap: wrap;
			@include media('>=full-hd') {
				margin-top: 40px;
				gap: 40px;
			}
		}

		&-bottom {
			margin-top: 20px;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 12px;
			@include media('>=desktop') {
				margin-top: 30px;
			}
		}

		&-date {
			color: rgb(255, 255, 255,0.71);
			margin: 0px;
			font-weight: 300;
			@include media('>=desktop') {
				font-size: 18px;
			}
		}

		&-socials {
			display: flex;
			align-items: center;
			gap: 12px;
		}

		&-social {
			background-color: #005149;
			border-radius: 50%;
			width: 30px;
			height: 30px;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			color: black;
			font-size: 18px;
			font-weight: 700;
			transition: all 0.25s ease 0s;
			&:hover {
				background-color: #008f81;
			}
		}
	}

	.swiper-pagination {
		&-wrapper {
			position: absolute;
			width: 100%;
			z-index: 3;
			left: 0;
			bottom: 60px;
			@include media('>=desktop') {
				bottom: 80px;
			}
		}
		position: static;
		text-align: left;
		&-bullet {
			width: 20px;
			height: 20px;
			opacity: 1;
			background-color: #00a6d8;
			transition: all 0.25s ease 0s;

			&.swiper-pagination-bullet-active {
				background-color: #80d3eb;
			}
		}
	}
}