.team-article-section {
	background-color: #0e283b;
	padding: 40px 0px;
	@include media('>=desktop') {
		padding: 60px 0px;
	}
	@include media('>=widescreen-lg') {
		padding: 80px 0px;
	}
	@include media('>=full-hd') {
		padding: 100px 0px;
	}

	h2 {
		margin: 0px;
		font-size: 32px;
		color: #639590;
		font-weight: 400;
		@include media('>=desktop') {
			font-size: 48px;
		}
	}

	&-body {
		margin-top: 30px;
		display: grid;
		grid-template-columns: 1fr;
		@include media('>=desktop') {
			grid-template-columns: 1fr 1fr;
		}
		@include media('>=desktop') {
			margin-top: 60px;
			grid-template-columns: repeat(4,1fr);
		}
	}

	&-item {
		outline-left: 0.5px solid #9ca5ac;
		outline-right: 0.5px solid #9ca5ac;
		position: relative;
		
		&-content {
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 2;
			width: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			text-align: center;
			gap: 10px;
			padding: 16px;
			background-color: rgb(0, 166, 216,0.8);

			* {
				color: rgb(255, 255, 255,0.71);
			}
		}
	}

	&-name {
		font-weight: 300;
		font-size: 18px;
	}

	&-link {
		font-weight: 700;
	}
}