.article-section {
	padding: 40px 0px;
	@include media('>=desktop') {
		padding: 60px 0px;
	}
	@include media('>=widescreen-lg') {
		padding: 80px 0px;
	}
	@include media('>=full-hd') {
		padding: 100px 0px;
	}
	&-body {
		display: grid;
		gap: 20px;
		@include media('>=tablet') {
			grid-template-columns: calc(70% - 10px) calc(30% - 10px);
		}

		@include media('>=widescreen-lg') {
			gap: 50px;
			grid-template-columns: calc(75% - 25px) calc(25% - 25px);
		}
	}

	&-sidebar {
		h2 {
			margin: 0px;
			color: #0ca9d9;
			font-size: 32px;
			font-weight: 400;
			.insight-article & {
				margin-top: 30px;

				@include media('>=widescreen-lg') {
					margin-top: 60px;
				}
			}

			@include media('>=tablet') {
				font-size: 40px;
			}
		}
	}

	&-related {
		margin-top: 20px;
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	.insights-section-slide-card-text {
		font-size: 16px;
		@include media('>=desktop') {
			font-size: 18px;
			padding: 16px;
		}
	}

	&-content {
		* {
			font-weight: 300;
			color: rgb(255, 255, 255,0.71);
			font-family: "Roboto", serif;
			line-height: 150%;
		}
		h2 {
			margin: 0px;
			font-size: 24px;
			color: #4d8580;
			font-weight: 700;
			@include media('>=desktop') {
				font-size: 28px;
			}
			@include media('>=widescreen-lg') {
				font-size: 32px;
			}
			@include media('>=full-hd') {
				font-size: 40px;
			}

			.insight-article & {
				color: #00a6d8;
				font-size: 16px;
				@include media('>=desktop') {
					font-size: 16px;
				}
				@include media('>=widescreen-lg') {
					font-size: 24px;
				}
				@include media('>=full-hd') {
					font-size: 28px;
				}
			}
		}

		h3 {
			margin: 0px;
			margin-top: 20px;
			color: #4d8580;
			font-size: 18px;
			font-weight: 700;
			@include media('>=desktop') {
				font-size: 20px;
			}
			@include media('>=widescreen-lg') {
				font-size: 22px;
			}
			@include media('>=full-hd') {
				font-size: 24px;
			}

			.insight-article & {
				color: #00a6d8;
				font-size: 16px;
				@include media('>=desktop') {
					font-size: 16px;
				}
				@include media('>=widescreen-lg') {
					font-size: 18px;
				}
				@include media('>=full-hd') {
					font-size: 20px;
				}
			}
		}

		strong,
		b {
			font-weight: 700;
		}

		.quote {
			display: flex;
			gap: 10px;
			align-items: start;
			margin-top: 20px;
			background-color: #014c46;
			padding: 20px;

			@include media('>=widescreen-lg') {
				font-size: 18px;
			}

			svg {
				display: block;
				flex-shrink: 0;
				width: 40px;
				height: 40px;
				path {
					fill: #1a625b;
				}
			}
		}
	}

	&-bottom {
		margin-top: 30px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 20px;
		flex-wrap: wrap;

		@include media('>=desktop') {
			margin-top: 50px;
		}
	}

	&-back {
		flex-shrink: 0;
	}

	&-pagination {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex: 1 0;

		&-links {
			display: flex;
			align-items: center;
			gap: 12px;
			justify-content: center;
			@include media('>=desktop') {
				gap: 24px;
			}
		}

		&-link {
			padding: 4px;
			font-weight: 300;
			transition: all 0.25s ease 0s;
			&.active,
			&:hover {
				font-weight: 700;
				color: #005149;
				.insight-article & {
					color: #00a6d8;
				}
			}
		}
	}

	&-teams {
		margin-top: -80px;
		position: relative;
		z-index: 2;
		display: flex;
		flex-direction: column;
		gap: 20px;
		@include media('>=tablet') {
			margin-top: -80px;
		}
		@include media('>=desktop') {
			margin-top: -105px;
		}
		@include media('>=widescreen-lg') {
			margin-top: -140px;
		}
		@include media('>=full-hd') {
			margin-top: -155px;
		}

		&-card {
			width: 100%;
			display: flex;

			&-image {
				width: 80px;
				flex-shrink: 0;
				@include media('>=widescreen-lg') {
					width: 100px;
				}
			}
			&-content {
				flex: 1 1 auto;
				background-color: rgb(0, 166, 216,0.8);
				padding: 12px;
				@include media('>=widescreen-lg') {
					padding: 20px;
				}
			}
			&-name {
				margin: 0px;
				font-weight: 400;
				font-size: 18px;
				@include media('>=widescreen-lg') {
					font-size: 20px;
				}
				@include media('>=widescreen-lg') {
					font-size: 22px;
				}
			}
			&-job-title {
				margin: 0px;
				margin-top: 10px;
				color: rgb(255, 255, 255,0.71);
				font-weight: 700;
				font-size: 18px;
				@include media('>=widescreen-lg') {
					font-size: 20px;
				}
				@include media('>=full-hd') {
					font-size: 22px;
				}
			}
		}
	}
}